<template lang="pug">
.page-container
    el-row(:gutter="20")
        el-col(:sm="24" :md="12")
            .page-title 心理師列表
            select.labelSelector(v-model='semester_label')
                option(value='') 請選擇學期標籤
                option(v-for='option in labelOption' :key='option.label' :value='option.label') {{ option.label }}
            el-table.list-container(:data="counselors" style="width: 100%")
                el-table-column(prop="name" label="姓名")
                el-table-column(prop="semester_label" label="學期")
                el-table-column(prop="available_time" label="可諮商時段")
                    template(#header='')
                        el-popover(placement="right" title="" :width="200" trigger="hover" popper-class="weektimePop")
                            div(v-html="getHtmlContent()")
                            template(#reference)
                                span 可諮商時段
                                    QuestionFilled(style="width: 1em; height: 1em; padding: 0 0 0 3px")
                    template(#default='scope')
                        div {{scope.row.available_time}}
                el-table-column(label="功能按鈕")
                    template(v-slot:default="slotProps") 
                        el-button(type="primary" size="small"  @click="modifyCase(slotProps.row.id)") 修改
                        el-button(type="danger" size="small" @click="clickDelete(slotProps.row.id)") 刪除
        el-col(:sm="24" :md="12")
            router-view(v-slot="{ Component }" @modified='getCounselor()')
                transition(name="scale" mode="out-in")
                    component(:is="Component")
</template>

<script>
import {weektimeMixin} from "@/components/weektimeMixin"
export default {
    mixins: [weektimeMixin],
    data () {
        return {
            semester_label: '',
            labelOption: [],
        }
    },
    async created() {
        await this.$store.dispatch('fetchSemesterLabel')
        await this.$store.dispatch('fetchCounselors')
        this.labelOption = this.$store.state.semester_label.data
    },
    computed: {
        counselors() {
            if (this.semester_label !== '')
                return this.$store.state.counselors.filter(counselor => counselor.semester_label === this.semester_label)
            else
                return this.$store.state.counselors
        }
    },
    methods: {
        getCounselor() {
            this.$store.dispatch('fetchCounselors')
        },
        deleteCounselor(id) {
            this.$http.delete("/api/counselor/"+id)
            .then(response => {
                console.log(response.data.message)
                this.$message({
                    type: 'success',
                    message: '刪除成功!'
                });
                this.getCounselor()
            })
            .catch((error) => {
                //發送請求失敗時
                console.log(error)
            });
        },
        clickDelete(id) {
            this.$confirm('將永久刪除此筆資料, 是否繼續?', '提示', {
                confirmButtonText: '確定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    this.deleteCounselor(id)
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消刪除'
                })         
            })
        },
        modifyCase(id) {
            this.$router.push('/manageCounselor/modify/'+id)
        }
    }
}
</script>
<style>
.scale-enter-active,
.scale-leave-active {
  transition: all 0.2s ease;
}
.scale-enter-from,
.scale-leave-to {
  opacity: 0;
  transform: scale(0.6);
}
</style>
<style lang="sass" scoped>
@import "@/style/common.sass"
.labelSelector
    margin: 0 10px 10px 0

</style>
