export const weektimeMixin = {
    data() {
        return {
            days: [
                {label: 'ㄧ',value: 'M',name: "Monday"},
                {label: '二',value: 'T',name: "Tuesday"},
                {label: '三',value: 'W',name: "Wednesday"},
                {label: '四',value: 'R',name: "Thursday"},
                {label: '五',value: 'F',name: "Friday"}
            ],
            times: [
                {label: '09:00 ~ 09:50',value: 1},
                {label: '10:00 ~ 10:50',value: 2},
                {label: '11:00 ~ 11:50',value: 3},
                {label: '13:00 ~ 13:50',value: 'n'},
                {label: '14:00 ~ 14:50',value: 4},
                {label: '15:00 ~ 15:50',value: 5},
                {label: '16:00 ~ 16:50',value: 6},
                {label: '17:00 ~ 17:50',value: 'e'},
                {label: '18:00 ~ 18:50',value: 7},
                {label: '19:00 ~ 19:50',value: 8},
                {label: '20:00 ~ 20:50',value: 9}
            ],
            dayRule: {'M':1,'T':2,'W':3,'R':4,'F':5},
            timeRule: {'1':1,'2':2,'3':3,'n': 4,'4':5,'5':6,'6':7,'e':8,'7':9,'8':10,'9':11},
        }
    },
    computed: {
        holiday: function(){
            return this.$store.state.holiday.data
        }
    },
    methods: {
      convertUTCtoStr(timestamp) {
        let date = new Date(timestamp)
        return date.getFullYear()+'-'+(date.getMonth()+1)+'-'+date.getDate()
      },
      getHtmlContent(){
        var content = "<strong>星期代碼</strong> (以英文字母表示)<br><table>"
        this.days.forEach(element => {
            content+= `<tr><td style="text-align: center">${element.value}</td><td>星期${element.label} ${element.name}</td></tr>`
        })
        content+= "</table><br><strong>節次與時間</strong> <br><table>"
        this.times.forEach(element => {
            content+= `<tr><td style="text-align: center">${element.value}</td><td>${element.label}</td></tr>`
        })
        content+= "</table>"
        return content
      },
      countRangeDay(_endDate , _startDate) {
        let endDate = new Date(_endDate)
        let startDate = new Date(_startDate)
        const msInDay = 1000 * 60 * 60 * 24
        if(endDate - startDate < 0)
            return 0
        let num = Math.ceil((endDate - startDate) / msInDay)
        return num
      },
      countConsultationNumber(_endDate , _startDate) {
        let endDate = new Date(_endDate)
        let startDate = new Date(_startDate)
        const msInWeek = 1000 * 60 * 60 * 24 * 7
        const msInDay = 1000 * 60 * 60 * 24
        if(endDate - startDate < 0 && endDate - startDate > -msInWeek) {
          if(endDate - startDate > -msInDay) {
            return 1
          }
          return 0
        }
        let num = Math.ceil(Math.abs(endDate - startDate) / msInWeek)+1
        let idxDate = new Date(_endDate)
        idxDate.setDate(idxDate.getDate() -7 )
        while(idxDate >= startDate) {
          if (this.checkHoliday(idxDate) )
            num-=1
          idxDate.setDate(idxDate.getDate() -7 )
        }
        return num
      },
      checkRangeDay(offset,end,start,status) {
        let today = new Date()
        today.setDate(today.getDate() + (offset))
        const msInDay = 1000 * 60 * 60 * 24;
        var startDate = new Date(start)
        if(status == "assign") {
            if( Math.floor((today - startDate) / msInDay)>=0 ) {
                return true
            }
            return false
        }
        var endDate = new Date(end)
        if( (endDate - today) / msInDay > -1 && Math.floor((today - startDate) / msInDay)>=0 ) {
            return true
        }
        return false
      },
      checkReserve(offset,start) {
        let today = new Date()
        today.setDate(today.getDate() + (offset))
        const msInDay = 1000 * 60 * 60 * 24;
        var startDate = new Date(start)
        if( Math.floor((startDate - today) / msInDay) > -1 ) {
            return true
        }
        return false
      },
      checkHoliday(date) {
        if (!this.holiday) return false
        const holidayDay = this.holiday.map(e => new Date(e.date))
        let finding = holidayDay.find(e => this.checkSameDay(e, date))
        if (finding) return true
        return false
      },
      checkSameDay(date1, date2) {
        if (
            date1.getFullYear() === date2.getFullYear() &&
            date1.getMonth() === date2.getMonth() &&
            date1.getDate() === date2.getDate()
          ) return true
          return false
      }
    }
}